* {
    margin: 0;
    padding: 0;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    list-style: none;
    text-decoration: none;
}

.th-component {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: start;
        align-items: flex-start;
    -webkit-transition: 1s;
    -o-transition: 1s;
    transition: 1s;
    width: 152px;
    position: relative;
    padding: 10px;
}

.th-component:hover .th__overlay {
    opacity: 1;
}

.th__overlay {
    position: absolute;
    top: 10px;
    left: 10px;
    bottom: 10px;
    right: 10px;
    background-color: rgba(0, 0, 0, 0.5);
    border-radius: 5px;
    opacity: 0;
    padding: 10px;
}

.img-compare {
    border-radius: 5px;
    display: block;
    height: 200px;
    width: 142px;
}

.cp-book-comparison {
    width: 20px;
    height: 20px;
    position: absolute;
    top: 10px;
    right: 10px;
    cursor: pointer;
}

.cp-preview {
    font-size: 15px;
    color: #fff;
    text-transform: uppercase;
    font-weight: bold;
    padding: 0.5rem 0.25rem;
    border: 5px solid #fff;
    text-align: center;
    width: 80%;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    position: absolute;
    -webkit-transition: .3s;
    -o-transition: .3s;
    transition: .3s;
    cursor: pointer;
}

.cp-preview:hover {
    background-color: #fff;
    color: #000;
}

.cp-book-info {
    position: absolute;
    bottom: 10px;
    right: 10px;
    width: 20px;
    height: 20px;
    cursor: pointer;
}


.cp-cart-icon {
    position: absolute;
    width: 20px;
    height: 20px;
    display: block;
    bottom: 10px;
    left: 10px;
    cursor: pointer;
}

.cp-book-icon {
    position: absolute;
    top: 10px;
    left: 10px;
    width: 20px;
    height: 20px;
    cursor: pointer;
}

.cp-book-icon path:hover {
    fill: #000;
}


