*{
    padding: 0;
    margin: 0;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    list-style: none;
    text-decoration: none;
}

.wrapper {
    background-color: #fff;
    width: 100%;
}

.container {
    padding: 0 20px;
    margin: 0 auto;
}

.header {
    /*width: 100%;*/
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
    text-align: center;
    position: relative;

}
.title {
    text-transform: uppercase;
    font-size: 2rem;
    font-weight: 700;
    color: #000000;
    text-align: center;
    padding: 2.5rem;
    letter-spacing: .75rem;
}

.btn {
    height: 2.2rem;
    width: 2.2rem;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    background-color: inherit;
    border: none;
}

.btn-menu {
    /*background-image: url(/menu.png);*/
    outline: none;
    position: relative;
    -webkit-transition: .5s;
    -o-transition: .5s;
    transition: .5s;
}
.btn-menu-line1 {
    position: absolute;
    -webkit-transform-origin: left;
        -ms-transform-origin: left;
            transform-origin: left;
    display: block;
    width: 100%;
    height: 4px;
    background: #000;
    top: 0;
    left: 0;
    -webkit-transition: .5s;
    -o-transition: .5s;
    transition: .5s;
}

.btn-menu-line2 {
    position: absolute;
    display: block;
    -webkit-transform: translate(0, -50%);
        -ms-transform: translate(0, -50%);
            transform: translate(0, -50%);
    width: 100%;
    height: 4px;
    background: #000;
    top: 50%;
    left: 0;
    -webkit-transition: .5s;
    -o-transition: .5s;
    transition: .5s;
}

.btn-menu-line3 {
    position: absolute;
    display: block;
    -webkit-transform-origin: left;
        -ms-transform-origin: left;
            transform-origin: left;
    width: 100%;
    height: 4px;
    background: #000;
    bottom: 0;
    left: 0;
    -webkit-transition: .5s;
    -o-transition: .5s;
    transition: .5s;
}

.btn-menu-line2-active  {
    width: 0;
    -webkit-transition: .5s;
    -o-transition: .5s;
    transition: .5s;
}

.btn-menu-line1-active {
    width: 2.85rem;
    -webkit-transform: rotateZ(40deg);
        -ms-transform: rotate(40deg);
            transform: rotateZ(40deg);
    -webkit-transition: .5s;
    -o-transition: .5s;
    transition: .5s;
}

.btn-menu-line3-active {
    width: 2.85rem;
    -webkit-transform: rotateZ(-40deg);
        -ms-transform: rotate(-40deg);
            transform: rotateZ(-40deg);
    -webkit-transition: .5s;
    -o-transition: .5s;
    transition: .5s;
}

.btn-cart {
    background-image: url(/cart.png);
}

.cart {
    position: relative;
}
.cart__counter {
    position: absolute;
    background: #000;
    bottom: -9px;
    right: -9px;
    color: #ffff;
    width: 24px;
    height: 24px;
    line-height: 24px;
    border-radius: 50%;
    font-weight: 700;
}


/*for desktop */
@media (min-width: 1200px) {

    .header {
        display: -ms-flexbox;
        display: flex;
        -ms-flex-pack: justify;
            justify-content: space-between;
        padding: 2.5rem 0;
    }
    .wrapper {
        background-color: #f5f5f5;
    }

    .container {
        /*min-width: 1400px;*/
        padding: 0 140px;
    }

    .title {
        display: none;
    }
    .btn-menu{
        display: -ms-flexbox;
        display: flex;

    }

    .nav {
        display: -ms-flexbox;
        display: flex;
        font-size: 1rem;
        font-weight: 400;
        text-transform: uppercase;
        color: #b8b8b8;
        width: 85%;
    }

    .menu {
        width: 100%;
        display: -ms-flexbox;
        display: flex;
        -ms-flex-pack: distribute;
            justify-content: space-around;
    }

    .menu__item:hover {
        color: #000000;
    }

}



