.book-section {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: space-evenly;
        justify-content: space-evenly;
}

.card {
    -ms-flex-preferred-size: 130px;
        flex-basis: 130px;
    margin-bottom: 20px;
    font-family: sans-serif;
    -webkit-transition: 1s;
    -o-transition: 1s;
    transition: 1s;
}

.card:hover .card__overlay {
    opacity: 1;
}

.card__cont {
    position: relative;
}

.card__overlay {
    position: absolute;
    top: 0;
    left: 0;
    background-color: rgba(0,0,0,0.5);
    width: 100%;
    height: 100%;
    border-radius: 5px;
    opacity: 0;
}

.card__pic {
    margin-bottom: 10px;
    border-radius: 5px;
    display: block;
    height: 200px;
}

.card__title {
    font-size: 14px;
    margin-bottom: 5px;
}

.card__author {
    font-size: 12px;
    color: #a9a9a9;
    font-style: italic;
    margin-bottom: 5px;
}

.card__price {
    font-size: 12px;
    color: #a9a9a9;
}

.preview {
    font-size: 15px;
    color: #fff;
    text-transform: uppercase;
    font-weight: bold;
    padding: 0.5rem 0.25rem;
    border: 5px solid #fff;
    text-align: center;
    width: 80%;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    position: absolute;
    -webkit-transition: .3s;
    -o-transition: .3s;
    transition: .3s;
    cursor: pointer;

}

.preview:hover {
    background-color: #fff;
    color: #000;
}

.buy {
    position: absolute;
    bottom: 10px;
    color: #fff;
    display: -ms-flexbox;
    display: flex;
    width: 100%;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
    cursor: pointer;
    padding: 0 15px;
}

.cart-icon {
    width: 20px;
    height: 20px;
    display: block;
}

.book-icon {
    position: absolute;
    top: 10px;
    right: 10px;
    width: 20px;
    height: 20px;
}

.book-comparison {
    position: absolute;
    top: 10px;
    left: 10px;
    width: 20px;
    height: 20px;
}

.book-button {
    display: block;
    position: absolute;
    right:10%;
    top:14%;
    border: none;
    background: none;
    font-style: italic;
    color:  #000000;
    font-weight: 500;
    background: transparent;
    background-image: url("/cart.png");
    background-position: center;
    background-size: contain;
    height: 25px;
    width: 25px;
}


.book-info {
      position: absolute;
      bottom: 1px;
      right: 10px;
      width: 20px;
      height: 20px;
  }


