@import url("https://fonts.googleapis.com/css?family=Open+Sans");

::-moz-selection {
    background-color: #95a5a6;
}

::selection {
    background-color: #95a5a6;
}
html {
    height: 100%;
}
/*body {*/
    /*background: url(R6ZWbAb.jpg);*/
    /*background-size: cover;*/
    /*background-repeat: no-repeat;*/
/*}*/
img {
    height: auto;
    width: 100%;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}


.bold {
    font-weight: bold;

}
.curs {
    font-style: italic;
}
.mline {
    border: 2px solid #ecf0f1;
    width: 14%;
    margin: 0 auto;
}
.line {
    border: 2px solid #ECF0F1;
    width: 100%;
    margin: 0 auto;
}
.happy{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-pack: justify;
        justify-content: space-between;
}
.avatar{
    display: block;
    height: 380px;
}
#wrapper {
    -ms-flex-preferred-size: 30%;
        flex-basis: 30%;
    text-align: center;
    color: #212129;
    background-color: #fff;
    -webkit-box-shadow: 0 0 30px #C7A569;
            box-shadow: 0 0 30px #C7A569;
    max-width: 380px;
    min-height: 600px;
    margin: 20px auto;
    position: relative;
    z-index: 0;
}
.arrow {
    cursor: pointer;
    position: absolute;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: start;
        justify-content: flex-start;
    -ms-flex-align: start;
        align-items: flex-start;
    margin: 10px 10px;
}
.bars {
    cursor: pointer;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: end;
        justify-content: flex-end;
    -ms-flex-align: start;
        align-items: flex-start;
}
.bars .fa {
    margin: 20px 20px;
}
.header-card {
    /*background: url(javascript-promises.jpg);*/
    -webkit-box-shadow: inset 0 0 60px 20px rgba(0,0,0,0.5);
            box-shadow: inset 0 0 60px 20px rgba(0,0,0,0.5);
    min-height: 300px;
    background-repeat: no-repeat;
    background-size: cover;
}
.name-name{

}
.name h1 {
    color: rgba(105, 86, 100, 0.57);
    font-size: 1.1em;

}
.info {
    color: #212129;
    font-size: 0.8em;
    margin: 20px 0 20px 0;
}
/*START START START colors but */
.header .fa {color: #ecf0f1;-webkit-transition: 250ms;-o-transition: 250ms;transition: 250ms;}
.header .fa:hover {color: #212129; -webkit-transition: 250ms; -o-transition: 250ms; transition: 250ms;}
.info .fa {color: #212129; margin-top: 1px;}
.contacts .fa {font-size: 1.2em;}
.contacts .fa:hover {opacity:0.75;-webkit-transition:225ms;-o-transition:225ms;transition:225ms;}
.fa-facebook {color: #1F3A93;-webkit-transition:350ms;-o-transition:350ms;transition:350ms;}
.fa-envelope-o {color: #212129;-webkit-transition:350ms;-o-transition:350ms;transition:350ms;}
.fa-google-plus {color: #F22613;-webkit-transition:350ms;-o-transition:350ms;transition:350ms;}
.fa-instagram {color: #BF55EC;-webkit-transition:350ms;-o-transition:350ms;transition:350ms;}
.fa-twitter {color: #22A7F0;-webkit-transition:350ms;-o-transition:350ms;transition:350ms;}
.footer .fa:hover {font-size: 1.1em;-webkit-transition:200ms;-o-transition:200ms;transition:200ms;-webkit-transform: rotateX(180deg);transform: rotateX(180deg);}
/* colors but END END END*/
.contacts li {
    display: inline-block;
    margin: 12px 16px;
}
.button {
    margin: 34px 0 44px 0;
}
.follow {
    color: #212129;
    border: 3px #212129 solid;
    border-radius: 30px;
    padding: 6px 60px;
    background: #fff;
    -webkit-transition: 220ms;
    -o-transition: 220ms;
    transition: 220ms;
}
.follow:hover {
    color: #fff;
    border-radius: 30px;
    padding: 7px 72px;
    background: #212129;
    -webkit-transition: 220ms;
    -o-transition: 220ms;
    transition: 220ms;
}
.stats {
    font-size: 0.9em;
    padding: 15px 0;
}
.stats li{
    display: inline-block;
    margin: 0 27px;
}
.footer {
    padding: 15px 0;
}
.footer .fa {
    cursor: pointer;
    color: #212129;
    -webkit-transition: 150ms;
    -o-transition: 150ms;
    transition: 150ms;
}

@media (min-width: 1024px) {
    .bold {
        font-size: 14px;
    }
    .name-name h1 {
        font-size: 18px;
    }
    .name h1 {
        font-size: 18px;
    }

    .follow {
        font-size: 14px;
    }

    .level {
        font-size: 14px;
    }

    .contacts .fa {
      font-size: 2rem;

    }

}



